@use '../global' as g;

.header-container {
  padding: 8px 8px;

  @media screen and (min-width: 768px) {
    margin-left: auto;
    margin-right: auto;
    padding: 8px 8px;
  }
}

.header-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;

  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
}

.header-logo {
  display: flex;
  align-items: center;
  gap: 8px;

  @media screen and (min-width: 768px) {
    display: flex;
    align-items: center;
    gap: 8px;
  }
}

.header-logo-image {
  width: 36px;
  height: 36px;

  @media screen and (min-width: 768px) {
    width: 100%;
    height: 100%;
    max-width: 60px;
    max-height: 60%;
  }
}

.head-logo-title {
  font-size: 12px;
  line-height: 1.2;
  color: #11b2d7;

  @media screen and (min-width: 768px) {
    font-size: 24px;
  }
}

/* ハンバーガーが閉じているとき */
.header-nav {
  display: none;

  &.is-active {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    width: 70%;
    height: 100vh;
    background: #fff;
    z-index: 100;
    padding: 60px 20px;
  }

  @media screen and (min-width: 768px) {
    margin-left: auto;
    position: static;
    width: auto;
    text-align: right;
    display: block;
  }
}

.header-nav__list {
  list-style: none;
  padding: 0;
  margin: 0;

  @media screen and (min-width: 768px) {
    display: flex;
    gap: 12px;
  }
}

.header-nav__item {
  font-size: 8px;

  @media screen and (min-width: 768px) {
    font-size: 16px;
  }
}

/* ハンバーガーメニューが開いているとき */
.header_inner {
  display: flex;
  justify-content: space-between;
  align-items: center;

  border-radius: 50px;
  opacity: 1;
  padding: 8px 24px;
}

.header-nav {

  @media screen and (min-width: 768px) {
    position: static;
    width: auto;
    height: auto;
    transform: translateX(0);
    display: block;
    background-color: transparent;

    transition: none;
  }
}

.header-nav_item {
  margin-top: 20px;

  @media screen and (min-width: 768px) {
    margin-top: 0;
  }
}


/*ハンバーガーメニュー三本線の配置*/
.hamburger-btn {
  display: block;
  width: 24px;
  height: 18px;
  display: flex;
  position: relative;
  flex-direction: column;
  justify-content: space-between;
  margin-left: auto;
  transition: all 0.5s;
  cursor: pointer;

  @media screen and (min-width: 768px) {
    display: none;
  }
}

/*ハンバーガーメニューの線の設定（閉じているとき）*/
.hamburger-btn_bar {
  display: block;
  position: absolute;
  width: 24px;
  height: 1px;
  background-color: #333;
  transition: all 0.5s;

  @media screen and (min-width: 768px) {
    display: none;
  }
}

/*1~3本目の線の設定*/
.hamburger-btn_bar:nth-child(1) {
  bottom: 16px;
}

.hamburger-btn_bar:nth-child(2) {
  bottom: 8px;
}

.hamburger-btn_bar:nth-child(3) {
  bottom: 0px;
}